import LineGradient from "../components/LineGradient";
import { useForm } from "react-hook-form";
import { motion } from "framer-motion";


const Contact =()=>{
    const{
        register,
        trigger,
        formState:{errors},
        
    } = useForm();

    const onSubmit = async (e) => {
      
      e.preventDefault();
  
   
      const isValid = await trigger(['name', 'email', 'message']);
      
      if (isValid) {
       
        e.target.submit();
      } 
    };

    return(
        <section id="contact" className="contact py-48">
          
        {/* HEADINGS */}
        <motion.div
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, x: 50 },
            visible: { opacity: 1, x: 0 },
          }}
          className="flex  w-full justify-center "
        >
          <div>
            <p className="font-playfair font-semibold text-4xl ">
              <span className="text-yellow ">CONTACT ME</span> TO GET STARTED
            </p>
            <div className="flex  my-5">
              <LineGradient width="w-1/2" />
            </div>
          </div>
        </motion.div>
        {/*FORM */}
        <motion.div
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.5 }}
          transition={{ delay: 0.2, duration: 0.5 }}
          variants={{
            hidden: { opacity: 0, y: 50 },
            visible: { opacity: 1, y: 0 },
          }}
          className="basis-1/2 mt-10  lg:justify-center lg:w-[30%] lg:ml-[35%]"
        >
          <form
            target="_blank"
            onSubmit={onSubmit}
            action="https://formsubmit.co/36f7929711fb9f8172780b68281b2334"
            method="POST"
          >
            <input
              className="w-full bg-grey font-semibold text-deep-blue placeholder-opaque-black p-3"
              type="text"
              placeholder="NAME"
              {...register("name", {
                required: true,
                maxLength: 100,
              })}
            />
            {errors.name && (
              <p className="text-red mt-1">
                {errors.name.type === "required" && "This field is required."}
                {errors.name.type === "maxLength" && "Max length is 100 char."}
              </p>
            )}

            <input
              className="w-full bg-grey font-semibold text-deep-blue placeholder-opaque-black p-3 mt-5"
              type="text"
              placeholder="EMAIL"
              {...register("email", {
                required: true,
                pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              })}
            />
            {errors.email && (
              <p className="text-red mt-1">
                {errors.email.type === "required" && "This field is required."}
                {errors.email.type === "pattern" && "Invalid email address."}
              </p>
            )}

            <textarea
              className="w-full bg-grey font-semibold text-deep-blue placeholder-opaque-black p-3 mt-5"
              name="message"
              placeholder="MESSAGE"
              rows="4"
              cols="50"
              {...register("message", {
                required: true,
                maxLength: 2000,
              })}
            />
            {errors.message && (
              <p className="text-red mt-1">
                {errors.message.type === "required" &&
                  "This field is required."}
                {errors.message.type === "maxLength" &&
                  "Max length is 2000 char."}
              </p>
            )}

            <button
              className="p-5 bg-yellow font-semibold text-deep-blue mt-5 hover:bg-red hover:text-white transition duration-500"
              type="submit"
            >
              SEND ME A MESSAGE
            </button>
          </form>
          </motion.div>
        
        </section>
    )
}

export default Contact;